import React, { useEffect, useState } from "react";
import {
  Modal,
  Slide,
  Box,
  Typography,
  Button,
  CircularProgress,
  useTheme,
  useMediaQuery,
} from "@mui/material";

interface WeatherProps {
  open: boolean;
  onClose: () => void;
}

interface ForecastDay {
  date: string;
  weather: string;
  emoji: string;
  temp: number;
  message: string;
  color: string;
}

const emojis: Record<string, string> = {
  sunny: "☀️",
  cloudy: "☁️",
  rainy: "🌧️",
  snowy: "❄️",
  storm: "⛈️",
  default: "🌈",
};

const Weather: React.FC<WeatherProps> = ({ open, onClose }) => {
  const [loading, setLoading] = useState(true);
  const [forecast, setForecast] = useState<ForecastDay[]>([]);
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down("sm"));

  useEffect(() => {
    if (open) {
      setLoading(true);
      setTimeout(() => {
        const dummy: ForecastDay[] = [
          { date: "Monday", weather: "sunny", emoji: emojis.sunny, temp: 26, message: "Delivery is fast today ☀️", color: "green" },
          { date: "Tuesday", weather: "cloudy", emoji: emojis.cloudy, temp: 22, message: "Might be a small delay", color: "orange" },
          { date: "Wednesday", weather: "rainy", emoji: emojis.rainy, temp: 18, message: "+20% delivery fee due to rain", color: "red" },
          { date: "Thursday", weather: "sunny", emoji: emojis.sunny, temp: 27, message: "Perfect delivery weather", color: "green" },
          { date: "Friday", weather: "storm", emoji: emojis.storm, temp: 20, message: "Delivery might be late", color: "red" },
          { date: "Saturday", weather: "cloudy", emoji: emojis.cloudy, temp: 21, message: "No delays expected", color: "orange" },
          { date: "Sunday", weather: "sunny", emoji: emojis.sunny, temp: 29, message: "Sunny and quick delivery ☀️", color: "green" },
        ];
        setForecast(dummy);
        setLoading(false);
      }, 1500);
    }
  }, [open]);

  return (
    <Modal open={open} onClose={onClose}>
      <Slide
        direction="up"
        in={open}
        mountOnEnter
        unmountOnExit
        timeout={{ enter: 400, exit: 300 }}
      >
        <Box
          sx={{
            position: "fixed",
            bottom: 0,
            left: 0,
            right: 0,
            height: "85vh",
            bgcolor: "white",
            zIndex: 1300,
            p: 3,
            overflowY: "auto",
            borderTopLeftRadius: 20,
            borderTopRightRadius: 20,
            boxShadow: 6,
            display: "flex",
            flexDirection: "column",
            gap: 3,
            backgroundImage: 'url("rainy.jpg")',
            backgroundSize: "cover",
            backgroundPosition: "center"
          }}
        >
          <Button
            onClick={onClose}
            variant="outlined"
            sx={{
              alignSelf: "flex-start",
              textTransform: "none",
              fontWeight: "bold",
              borderRadius: 2,
              px: 3,
              py: 1,
              mb: 1,
              color: "white",
              borderColor: "white"
            }}
          >
            Back
          </Button>

          {loading ? (
            <Box sx={{ textAlign: "center", mt: 5 }}>
              <CircularProgress color="secondary" />
              <Typography variant="h6" mt={2}>Loading forecast...</Typography>
            </Box>
          ) : (
            <Box sx={{ display: "flex", flexDirection: "column", gap: 2 }}>
              {forecast.map((day, index) => (
                <Box
                  key={index}
                  sx={{
                    display: "flex",
                    flexDirection: isMobile ? "column" : "row",
                    alignItems: isMobile ? "center" : "center",
                    justifyContent: "space-between",
                    bgcolor: "#87cfebdc",
                    borderRadius: 6,
                    px: 7,
                    py: 1,
                    gap: isMobile ? 0.5 : 2,
                    // maxWidth: 800,
                    height: 180,
                  }}
                >
                  <Typography variant="h5" fontWeight="bold" sx={{color: "white"}}>
                    {day.date}
                  </Typography>
                  <Typography fontSize={35} sx={{color: "white"}}>
                    {day.emoji} {day.temp}°C
                  </Typography>
                  <Typography
                    color={day.color}
                    fontSize={18}
                    sx={{ whiteSpace: "normal" }}
                  >
                    {day.message}
                  </Typography>
                </Box>
              ))}
            </Box>
          )}
        </Box>
      </Slide>
    </Modal>
  );
};

export default Weather;
